    import {
        mapState
    } from 'vuex'
	import { DynamicTabBar } from 'utils/utils.js'
	import CustomerHome from '../customer/home/home.vue'
	import SellerHome from '../seller/home/home.vue'
	// import tabBar from '../../components/tabBar/tabBar.vue'

    export default {
        computed: {
			...mapState(['forcedLogin', 'hasLogin', 'userName']),
			loginType(){
				const type = uni.getStorageSync('userName')
				return type === 'admin' ? 'seller' : 'customer'
			}
		},
		components:{
			CustomerHome,
			SellerHome
		},
        onLoad() {
            if (!this.hasLogin) {
                uni.showModal({
                    title: '未登录',
                    content: '您未登录，需要登录后才能继续',
                    /**
                     * 如果需要强制登录，不显示取消按钮
                     */
                    showCancel: !this.forcedLogin,
                    success: (res) => {
                        if (res.confirm) {
							/**
							 * 如果需要强制登录，使用reLaunch方式
							 */
                            if (this.forcedLogin) {
                                uni.reLaunch({
                                    url: '../login/login'
                                });
                            } else {
                                uni.navigateTo({
                                    url: '../login/login'
                                });
                            }
                        }
                    }
                });
            }
        },
		onShow() {
			DynamicTabBar()
		}
    }
